'use client'

import { fetchConfig } from "@/api/fetchConfig"
import { Author } from "@/components/Author/Author"
import { AuthorTasksInProcess } from "@/components/Author/AuthorTasksInProcess/AuthorTasksInProcess"
import { TopInfoTaskAuthor } from "@/components/TopInfoTaskAuthor/TopInfoTaskAuthor"
import { Button } from "@/components/ui/Button/Button"
import { useAppSelector } from "@/hooks/useAppSelector"
import { api } from "@/pages/api/api"
import { APICOMMAND } from "@/shared/types/command.types"
import { Task } from "@/shared/types/task.types"
import { useEffect, useState } from "react"


export default function page() {

    const [stateTabs, setStateTabs] = useState('process')
    const [tasksAuthor, setTasksAuthor] = useState<Task[] | null>(null)

    useEffect(() => {
        const fetchTasksAuthor = async () => {
            const config = await fetchConfig()
            const responseUsers = await api(APICOMMAND.GETAUTHORTASK, {}, config)
            const data = await responseUsers?.json()
            setTasksAuthor(data.data)
        }
        fetchTasksAuthor()
    }, [])
    
    console.log(tasksAuthor)

    return (
        <>
            <Author/>
        </>
    )
}
